@import './variables';
@import './overrides';

/** Ripple **/
.ripple-parent {
  position: relative;
  overflow: hidden;
}

/** Content **/
.content-gap {
  padding-top: 1.25rem;
}

.content-wrap {
  padding-left: 15px;
  padding-right: 15px;
  padding-bottom: 1.5rem;
}

.content-logo {
  width: 5rem;
  height: 5rem;
  object-fit: contain;
  margin: 0 auto;
}

.content-logo.is-small {
  width: 5rem;
  height: 5rem;
}

.content-logo.is-big {
  width: 7.5rem;
  height: 7.5rem;
}

.primary-content > *:last-child {
  margin-bottom: 0;
}

/** Forms **/
.btn-loading {
  position: relative;
}

.btn-loading-spinner {
  position: absolute;
  left: 0;
  top: 0;
  bottom: 0;
  right: 0;
  margin: auto;
  text-align: center;
}
