/** Engine Slides **/
.engine-slide {
  $width: 38vw;
  $height: $width * 4/3;
  $spacing: 1.625rem;

  position: relative;
  padding-top: 1.5rem;
  padding-bottom: 4.25rem;
  padding-left: $spacing/2;
  padding-right: $spacing/2;

  .engine-card {
    width: $width;
    height: $height;
    border-radius: .75rem;
    overflow: hidden;
    position: relative;
    z-index: 10;
    transform: scale(1);
    transition: 250ms ease-in-out transform;
    margin: 0 auto;

    .swiper-slide-active & {
      transform: scale(1.2);
    }

    img {
      width: 100%;
      height: 100%;
    }
  }

  .engine-info {
    position: absolute;
    bottom: 50%;
    left: 0;
    width: 100%;
    opacity: 0;
    transition: 250ms ease-in-out;
    transition-property: opacity, bottom;

    .swiper-slide-active & {
      opacity: 1;
      bottom: 0
    }
  }
}
